<template>
  <div class="card">
    <form>
      <div class="card-header">
        <h4 class="card-title">
          Wallet
        </h4>
      </div>
      <div class="card-content">
        <div class="form-group">
          <h2 class="ti-wallet">Account Balance:  {{model.balance}} £</h2>
        </div>
        <div class="form-group">
          <label>Charge Amount (£)</label>
          <input type="number"
                 name="amount"
                 v-validate="modelValidations.amount"
                 v-model="model.amount"
                 class="form-control">
          <small class="text-danger" v-show="amount.invalid">
            {{ getError('amount') }}
          </small>
        </div>
        <div class="form-group">
          <label>Password</label>
          <input type="password"
                 name="password"
                 v-validate="modelValidations.password"
                 v-model="model.password"
                 class="form-control">
          <small class="text-danger" v-show="password.invalid">
            {{ getError('password') }}
          </small>
          <p-checkbox v-model="model.getreward">Charge 10£, Get 10£</p-checkbox>
        </div>
      </div>
      <div class="card-footer text-center">
        <button class="btn btn-fill btn-info btn-wd" @click.prevent="validate()">Charge now</button>

      </div>
    </form>

  </div>
</template>
<script>
  import {mapFields} from 'vee-validate'
  import swal from 'sweetalert2'
  import axios from 'axios'

  export default {
    computed: {
      ...mapFields(['amount', 'password', 'balance'])
    },
    data () {
      return {
        model: {
          amount: '',
          password: '',
          balance: '0',
          getreward: false
        },
        balanceForm: new FormData(),
        modelValidations: {
          amount: {
            required: true,
            max: 3
          },
          password: {
            required: true,
            min: 8
          }
        }
      }
    },
    methods: {
      getError (fieldName) {
        return this.errors.first(fieldName)
      },
      validate () {
        if (this.model.amount >= 10 && this.model.getreward) {
          this.model.amount = parseFloat(this.model.amount) + 10
        }
        // console.log(JSON.stringify(this.model))
        this.$validator.validateAll().then((result) => {
          // console.log('test')
          // if(result)
          // add money to pocket
          let addMoneyFormDate = new FormData()
          addMoneyFormDate.append('Customer_id', sessionStorage.getItem('customer_id'))
          addMoneyFormDate.append('password', this.model.password)
          addMoneyFormDate.append('amount', this.model.amount)
          if (result) {
            axios.post('api/customer/addmoney/', addMoneyFormDate).then(
              response => {
                // console.log(response.data)
                if (response.data === 'Successfully added money in the wallet') {
                  // add money success
                  this.model.balance = parseFloat(this.model.amount) + parseFloat(this.model.balance)
                } else {
                  swal({
                    title: 'Error',
                    text: response.data,
                    type: 'warning',
                    confirmButtonClass: 'btn btn-success btn-fill',
                    buttonsStyling: false
                  }).catch(swal.noop)
                }
              }
            ).catch(
              function (error) {
                console.log(error)
              }
            )
          } else {
            swal({
              title: 'Error',
              text: 'Input Error',
              type: 'warning',
              confirmButtonClass: 'btn btn-success btn-fill',
              buttonsStyling: false
            }).catch(swal.noop)
          }
        })
      }
    },
    created () {
      // need to login
      // this.user = sessionStorage.getItem('userID')
      if (sessionStorage.getItem('loginStatus') !== '1') {
        swal({
          title: 'Error',
          text: 'Please login',
          type: 'warning',
          showCancelButton: true,
          confirmButtonClass: 'btn btn-success btn-fill',
          cancelButtonClass: 'btn btn-danger btn-fill',
          confirmButtonText: 'Login',
          buttonsStyling: false
        }).then(
          function () {
            // go to login page
            window.location = '#/customer/login'
          }, function (dismiss) {
            // dismiss can be 'overlay', 'cancel', 'close', 'esc', 'timer'
            if (dismiss === 'cancel') {
              window.location = '#/'
            }
          }
        )
      } else {
        this.balanceForm.append('Customer_id', sessionStorage.getItem('userID'))
        axios.post('api/customer/balance/', this.balanceForm).then(
          response => {
            this.model.balance = response.data
            // console.log('user balance: ' + this.model.balance)
            sessionStorage.setItem('balance', this.model.balance)
          }
        ).catch(
          function (error) {
            console.log(error)
          }
        )
      }
    }
  }
</script>
<style>
</style>



