<template>

  <div class="card">
    <div class="card-header">
      <h4 class="title">BikeShare, Green Lifestyle</h4>
      <p class="category">Please return the bike when you finished riding</p>
    </div>

        <div class="card">
          <div class="card-header">
            <div class="text-center">
              <b>BikeID:{{bikeID}}</b> &nbsp&nbsp
              <b>BikeBrand:{{bikeBrand}}</b>
            </div>
            <h4 class="title">Start</h4>
            <h4 class=" text-center">{{startTime}}</h4>
            <h4 class="title">End</h4>
            <h4 class=" text-center">{{endTime}}</h4>
          </div>
          <div class="card-content text-center">
            <h3>Click button to pay the bill</h3>
            <button class="btn btn-default btn-fill" @click="showSwal()">Pay Now</button>
            <button class="btn btn-default btn-fill" @click="reportdefective()">Report Defective Now</button>
          </div>
        </div>
      </div>

</template>
<script>
import swal from 'sweetalert2'
import axios from 'axios'

export default {
  data () {
    return {
      bikeID: '',
      bikeBrand: '',
      startTime: 'start time',
      endTime: 'end time'
    }
  },
  methods: {
    reportdefective () {
      swal({
        title: 'Report Defective this bill?',
        text: 'BikeID: ',
        type: 'warning',
        showCancelButton: true,
        confirmButtonClass: 'btn btn-success btn-fill',
        cancelButtonClass: 'btn btn-danger btn-fill',
        confirmButtonText: 'Yes !',
        buttonsStyling: false
      }).then(
        window.location = '#/customer/reportdefective'
      ).catch(swal.noop)
    },
    showSwal () {
      let paybillFormData = new FormData()
      paybillFormData.append('customer_id', sessionStorage.getItem('customer_id'))
      axios.post('api/customer/paybill/',
        paybillFormData).then(
          response => {
            // console.log(response.data)
            swal({
              title: `Thanks for riding, <br/> to achieve a green lifestyle. <br/> BIKESHARE `,
              buttonsStyling: false,
              confirmButtonClass: 'btn btn-success btn-fill'
            }).then(
              window.location = '#/customer/wallet'
            ).catch(swal.noop)
          }
      ).catch(
        window.location = '#/'
      )
    }
  },
  mounted () {
    this.bikeID = sessionStorage.getItem('bikeID')
    this.bikeBrand = sessionStorage.getItem('bikeBrand')
    this.startTime = sessionStorage.getItem('Start_time')
    this.endTime = sessionStorage.getItem('End_time')
  },
  created () {
    // need to login
    if (sessionStorage.getItem('loginStatus') !== '1') {
      // this.user = sessionStorage.getItem('userID')
      swal({
        title: 'Error',
        text: 'Please login',
        type: 'warning',
        showCancelButton: true,
        confirmButtonClass: 'btn btn-success btn-fill',
        cancelButtonClass: 'btn btn-danger btn-fill',
        confirmButtonText: 'Login',
        buttonsStyling: false
      }).then(
        function () {
          // go to login page
          window.location = '#/customer/login'
        },
        function (dismiss) {
          // dismiss can be 'overlay', 'cancel', 'close', 'esc', 'timer'
          if (dismiss === 'cancel') {
            window.location = '#/'
          }
        }
      ).catch(function () {
        // eslint-disable-next-line no-unused-expressions
        window.location = '#/'
      })
    } else if (sessionStorage.getItem('rentBikeStatus') === '1') {
      // bike rented
      this.bikeID = sessionStorage.getItem('bikeID')
      this.bikeBrand = sessionStorage.getItem('bikeBrand')
    }
  }

}

</script>
<style>

</style>



