<template>
  <div :class="{'nav-open': $sidebar.showSidebar}">
    <notifications transition-name="notification-list" transition-mode="out-in">

    </notifications>
    <transition name="fade"
                mode="out-in">
      <router-view></router-view>
    </transition>
  </div>
</template>

<script>
  // Loading some plugin css asynchronously
  import('sweetalert2/dist/sweetalert2.css')
  import('vue-notifyjs/themes/default.css')
  export default {
    created () {
      // init session data
      sessionStorage.setItem('userID', '') // username
      sessionStorage.setItem('customer_id', '') // customer get from backend
      sessionStorage.setItem('loginStatus', '') // 0 for logout, 1 for login
      sessionStorage.setItem('bikeID', '') // bikeID
      sessionStorage.setItem('rentBikeStatus', '') // 0 for rent bike failure, 1 for success
      sessionStorage.setItem('returnBikeStatus', '') // 0 for return bike failure, 1 for success
      sessionStorage.setItem('bikeBrand', '') // bike brand
      sessionStorage.setItem('balance', '') // user account balance
      sessionStorage.setItem('latitude', '') // record current user location
      sessionStorage.setItem('longitude', '') // record current user location
      sessionStorage.setItem('Ride_Bill', '') // ride bike bill
      sessionStorage.setItem('Start_time', '') // ride bike start time
      sessionStorage.setItem('End_time', '') // ride bike end time
      sessionStorage.setItem('session_key', '') // session_key from backend
    }
  }
</script>
<style lang="scss">
  .notifications.vue-notifyjs {
    .notification-list-move {
      transition: transform 0.3s, opacity 0.4s;
    }
    .notification-list-item {
      display: inline-block;
      margin-right: 10px;

    }
    .notification-list-enter-active, .notification-list-leave-active {
      transition: opacity 0.4s;
    }
    .notification-list-enter, .notification-list-leave-to /* .list-leave-active for <2.1.8 */
    {
      opacity: 0;
    }
  }

</style>



