<template>

    <div class="card">
      <div class="card-header">
        <h4 class="title">BikeShare, Green Lifestyle</h4>
        <p class="category">Sorry to bring inconvenience to you</p>
      </div>
      <div class="card">
        <div class="card-content text-center">
        <div class="col-6">
          <h5>Report defective bikeid :{{defectiveBike}}</h5>
          <label>Select defectives</label><br/>
          <el-select multiple class="select-primary"
                     size="large"
                     v-model="selects.multiple"
                     placeholder="Multiple Select">
            <el-option v-for="option in selects.defectives"
                       class="select-primary"
                       :value="option.value"
                       :label="option.label"
                       :key="option.label">
            </el-option>
          </el-select>
        </div>
        <div class="col-12">
          <label>Anything else</label>
          <textarea rows="5" class="form-control border-input" v-model="reportdetail"
                    placeholder="Here can be your description"></textarea>
        </div>
      </div>
      </div>

        <div class="card">
          <div class="card-content text-center">
            <h5>Click button to report defective</h5>
            <button class="btn btn-default btn-fill" @click="showSwal()">Report Now</button>
          </div>
        </div>
      </div>
</template>
<script>
  import swal from 'sweetalert2'
  import axios from 'axios'

  export default {
    data () {
      return {
        selects: {
          simple: '',
          defectives: [
            {value: 'Bicycle wheel damage', label: 'Bicycle wheel damage'},
            {value: 'Seat damage', label: 'Seat damage'},
            {value: 'Lock damage', label: 'Lock damage'},
            {value: 'Brakes failed', label: 'Brakes failed'},
            {value: 'Pedal damage', label: 'Pedal damage'},
            {value: 'Basked damage', label: 'Basked damage'}],
          multiple: ''
        },
        defectiveBike: '',
        reportdetail: ''
      }
    },
    methods: {
      showSwal () {
        let complain = this.reportdetail + this.selects.multiple.toString()
        // console.log(complain)
        let reportDefectiveFormData = new FormData()
        reportDefectiveFormData.append('customer_id', sessionStorage.getItem('customer_id'))
        reportDefectiveFormData.append('bike_id', sessionStorage.getItem('bikeID'))
        reportDefectiveFormData.append('complaint_description', complain)
        axios.post('api/customer/reportdefective/', reportDefectiveFormData).then(
          response => {
            // console.log(response.data)
            if (response.data === 'We have received your report.') {
              swal({
                title: 'Success',
                text: 'With your help, we will do better.  BIKESHARE ',
                buttonsStyling: false,
                confirmButtonClass: 'btn btn-success btn-fill'
              }).catch(swal.noop)
            }
          }
        )
        let paybillFormData = new FormData()
        paybillFormData.append('customer_id', sessionStorage.getItem('customer_id'))
        axios.post('api/customer/paybill/',
          paybillFormData).then(
          response => {
          }
        ).catch(
          window.location = '#/'
        )
        window.location = '#/'
      }
    },
    mounted () {
      // console.log('bike id', this.$route.params.id)
      this.defectiveBike = sessionStorage.getItem('bikeID')
    },
    created () {
      // need to login
      if (sessionStorage.getItem('loginStatus') !== '1') {
        // this.user = sessionStorage.getItem('userID')
        swal({
          title: 'Error',
          text: 'Please login',
          type: 'warning',
          showCancelButton: true,
          confirmButtonClass: 'btn btn-success btn-fill',
          cancelButtonClass: 'btn btn-danger btn-fill',
          confirmButtonText: 'Login',
          buttonsStyling: false
        }).then(
          function () {
            // go to login page
            window.location = '#/customer/login'
          }, function (dismiss) {
            // dismiss can be 'overlay', 'cancel', 'close', 'esc', 'timer'
            if (dismiss === 'cancel') {
              window.location = '#/'
            }
          }
        ).catch(swal.noop)
      }
    }
  }

</script>
<style>

</style>



