<template>


    <div>
      <a href="#/customer/wallet"><img src="static/img/ads/ad4.png" height="100" width="100%"/></a>
      <div class="card card-map">
        <div id="map" class="full-screen-map"></div>
      </div>

    </div>



</template>
<script>
  import GoogleMapsLoader from 'google-maps'
  import {API_KEY} from './API_KEY'
  import axios from 'axios'
  import swal from 'sweetalert2'
  GoogleMapsLoader.KEY = API_KEY

  function rentBike (SelectedBike) {
    // user login
    if (sessionStorage.getItem('loginStatus') === '1') {
      swal({
        title: 'Rent this bike?',
        text: 'BikeID: ' + SelectedBike.ID + ' BikeBrand: ' + SelectedBike.Brand,
        type: 'warning',
        showCancelButton: true,
        confirmButtonClass: 'btn btn-success btn-fill',
        cancelButtonClass: 'btn btn-danger btn-fill',
        confirmButtonText: 'Yes, rent it!',
        buttonsStyling: false
      }).then(
        function () {
          // rent bike
          // request backend rent this bike
          let rentbikeFormData = new FormData()
          rentbikeFormData.append('customer_id', sessionStorage.getItem('customer_id'))
          rentbikeFormData.append('bike_id', SelectedBike.ID)
          rentbikeFormData.append('latitude', SelectedBike.Lat)
          rentbikeFormData.append('longitude', SelectedBike.Lon)
          // console.log(rentbikeFormData.get('customer_id') + rentbikeFormData.get('bike_id') +
          //   rentbikeFormData.get('latitude') + rentbikeFormData.get('longitude'))
          axios.post('api/customer/rentbike/',
            rentbikeFormData)
            .then(response => {
              // swal(response.data)
              console.log(response.data)
              if (response.data === 'Start riding.') {
                // rent bike success from backend
                sessionStorage.setItem('bikeID', SelectedBike.ID)
                sessionStorage.setItem('bikeBrand', SelectedBike.Brand)
                sessionStorage.setItem('latitude', SelectedBike.Lat)
                sessionStorage.setItem('longitude', SelectedBike.Lon)
                sessionStorage.setItem('rentBikeStatus', 1)
                window.location = '#/customer/traceroutemap'
              } else {
                swal('Warning!', response.data, 'warning').then(window.location = '#/customer/unpaid')
              }
            })
            .catch(
              function (error) { // request error
                console.log(error)
              }
            )
        }
      ).catch(swal.noop)
    } else {
      swal({
        title: 'Error',
        text: 'Please login',
        type: 'warning',
        showCancelButton: true,
        confirmButtonClass: 'btn btn-success btn-fill',
        cancelButtonClass: 'btn btn-danger btn-fill',
        confirmButtonText: 'Login',
        buttonsStyling: false
      }).then(
        function (error) {
          console.log(error)
          // go to login page
          window.location = '#/customer/login'
        }
      ).catch(swal.noop)
    }
  }
  export default {
    data () {
      return {
        // simulate the coordinates
        bikes: [],
        location: new FormData()
      }
    },
    methods: {
      // notification
      initMap (google) {
        // Regular Map
        var myLatlng = new window.google.maps.LatLng(55.8009897568045, -4.31876927435126)
        var mapOptions = {
          zoom: 15,
          center: myLatlng,
          scrollwheel: false // we disable de scroll over the map, it is a really annoing when you scroll through page
        }
        var map = new window.google.maps.Map(document.getElementById('map'), mapOptions)
        var user = new window.google.maps.Marker({
          position: myLatlng,
          icon: 'static/img/faces/user.png',
          title: 'Center'
        })
        function currentlocMarker (pos, map) {
          map.setCenter(pos)
          user.setPosition(pos)
        }// get postion from browser
        navigator.geolocation.getCurrentPosition(
          function (position) {
            const pos = {
              lat: position.coords.latitude,
              lng: position.coords.longitude
            }
            this.location.set('latitude', position.coords.latitude)
            this.location.set('longitude', position.coords.longitude)
            // console.log('pos: ', pos)
            currentlocMarker(pos, map)
          },
          // eslint-disable-next-line handle-callback-err
          function (error) {
            // console.log('get location error')
            // get locatoin error
          },
          {
            enableHighAccuracy: true, // 是否要求高精度的地理位置信息
            timeout: 1000, // 对地理位置信息的获取操作做超时限制，如果再该事件内未获取到地理位置信息，将返回错误
            maximumAge: 60 * 1000 // 设置缓存有效时间，在该时间段内，获取的地理位置信息还是设置此时间段之前的那次获得的信息，超过这段时间缓存的位置信息会被废弃
          })
        user.setMap(map)
        map.addListener('zoom_changed', setCenter)
        map.addListener('dragend', setCenter)
        for (var i = 0; i < this.bikes.length; i++) {
          // var bikeloc = new google.maps.LatLng(bikes[i].Home_location_lat, bikes[i].Home_location_long);
          var eachbike = this.bikes[i]
          addBike(eachbike, map)
        }
        function setCenter () {
          var center = map.getCenter()
          // center.lat
          // console.log(center.toString())
          // set center mark location
          user.setPosition(center)
        }
        function addBike (location, map) {
          // Add the marker at the clicked location, and add the next-available label
          // from the array of alphabetical characters.
          var bikeloc = new google.maps.LatLng(eachbike['Latitude'], eachbike['Longitude'])
          const marker = new google.maps.Marker({
            position: bikeloc,
            map: map,
            icon: 'static/img/bike2.png'
          })
          // console.log('add:', eachbike['bike_id'], 'to map at', bikeloc)

          /* Selected Bike info */
          var SelectedBike = {
            ID: String(eachbike['bike_id']),
            Lat: String(eachbike['Latitude']),
            Lon: String(eachbike['Longitude']),
            Brand: String(eachbike['Brand'])
          }
          /**/
          marker.addListener('click', () => {
            rentBike(SelectedBike)
          })
        }
      }
    },
    mounted () {
      this.location.append('latitude', '55.8009897568045')
      this.location.append('longitude', '-4.31876927435126')
      axios.post('api/nearestbike/',
        // 'latitude=55.8009897568045&longitude=-4.31876927435126',
        this.location)
        .then(response => {
          this.bikes = response.data
          // console.log('bikes:  ' + this.bikes)
          GoogleMapsLoader.load((google) => {
            this.initMap(google)
            // console.log(this.$route.fullPath)
          })
        })
        .catch(
          function (error) { // request error
            // window.location = '#/customer/login'
            console.log(error)
            sessionStorage.setItem('error', error)
          }
        )
    }
  }
</script>
<style>
</style>



