<template>
  <div>
    <div class="row">
      <div class="col-md-8 col-md-offset-2">
        <div class="card card-wizard" id="wizardCard">
          <form-wizard shape="tab"
                       @on-complete="wizardComplete"
                       error-color="#EB5E28"
                       color="#66615B"
                       title="Bikeshare"
                       subtitle="Green way lifestyle">
            <tab-content title="User Register"
                         :before-change="validateFirstStep"
                         icon="ti-user">
              <first-step ref="firstStep"></first-step>
            </tab-content>

            <tab-content title="Personal Details"
                         :before-change="validateSecondStep"
                         icon="ti-settings">
              <second-step ref="secondStep"></second-step>
            </tab-content>

            <tab-content title="Last step"
                         icon="ti-check">
              <h2 class="text-center text-space">Bravo!
                <br>
                <small>Click on "<b>Finish</b>" to start our jounary</small>
              </h2>
            </tab-content>

            <button slot="prev" class="btn btn-default btn-fill btn-wd btn-back">Back</button>
            <button slot="next" class="btn btn-info btn-fill btn-wd btn-next">Next</button>
            <button slot="finish" class="btn btn-info btn-fill btn-wd">Finish</button>
          </form-wizard>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import {FormWizard, TabContent} from 'vue-form-wizard'
  import 'vue-form-wizard/dist/vue-form-wizard.min.css'
  import FirstStep from './Wizard/FirstStep.vue'
  import SecondStep from './Wizard/SecondStep.vue'
  import swal from 'sweetalert2'
  import axios from 'axios'

  export default {
    data () {
      return {
        wizardModel: {},
        userinfo: new FormData()
      }
    },
    components: {
      FormWizard,
      TabContent,
      FirstStep,
      SecondStep
    },
    methods: {
      validateFirstStep () {
        return this.$refs.firstStep.validate()
      },
      validateSecondStep () {
        return this.$refs.secondStep.validate()
      },
      wizardComplete () {
        // we gather models from all steps into one model
        this.wizardModel = {
          ...this.$refs.firstStep.model,
          ...this.$refs.secondStep.model
        }
        // constract userinfo
        this.userinfo.append('username', this.wizardModel.username)
        this.userinfo.append('password', this.wizardModel.password)
        this.userinfo.append('first_name', this.wizardModel.firstName)
        this.userinfo.append('last_name', this.wizardModel.lastName)
        this.userinfo.append('phone_number', this.wizardModel.phoneNumber)
        this.userinfo.append('cardNumber', this.wizardModel.creditCardNumber)
        this.userinfo.append('gender', this.wizardModel.gender)
        this.userinfo.append('age', this.wizardModel.age)
        // console.log(this.userinfo.get)
        sessionStorage.setItem('userID', this.userinfo.get('username'))
        sessionStorage.setItem('loginStatus', 0)
        axios.post('api/customer/signup/', this.userinfo)
              .then(response => {
                // console.log(response.data)
                if (response.data === 'Username already exists') {
                  swal({
                    text: `Username exists, please choose another username .  BIKESHARE `,
                    buttonsStyling: false,
                    confirmButtonClass: 'btn btn-success btn-fill'
                  }).catch(swal.noop)
                } else {
                  swal('Success!', 'Thanks for your time, please enjoy our services!',
                    'success').then(
                    function () {
                      sessionStorage.setItem('loginStatus', 1)
                      sessionStorage.setItem('customer_id', response.data[0].pk)
                      // console.log(response.data[0].pk)
                      // console.log(response.data[0].fields)
                      window.location = '#/'
                    }
                  ).catch(swal.noop)
                }
              })
              .catch(function (error) {
                console.log(error)
                swal({
                  title: 'something went wrong',
                  text: 'With your help,  we will do better.  BIKESHARE ',
                  buttonsStyling: false,
                  confirmButtonClass: 'btn btn-success btn-fill'
                }).then(
                  function () {
                    window.location = '#/customer/register'
                  }
                )
              }).catch(swal.noop)
      }
    }
  }
</script>
<style>
  .vue-form-wizard .wizard-icon-circle.tab_shape {
    background-color: #9A9A9A !important;
    color: white;
  }
</style>



