<template>
  <div>
    <h5 class="text-center">Please give us more details about your information.</h5>

    <div class="row">
      <div class="col-md-5 col-md-offset-1">
        <div class="form-group">
          <label class="control-label">
            First Name
          </label>
          <input class="form-control"
                 type="text"
                 name="firstName"
                 v-validate="modelValidations.firstName"
                 v-model="model.firstName"
                 placeholder="ex: Mike"
          />
          <small class="text-danger" v-show="firstName.invalid">
            {{ getError('firstName') }}
          </small>
        </div>
      </div>
      <div class="col-md-5">
        <div class="form-group">
          <label class="control-label">
            Last Name
          </label>
          <input class="form-control"
                 type="text"
                 name="lastName"
                 v-validate="modelValidations.lastName"
                 v-model="model.lastName"
                 placeholder="ex: Andrew"
          />
          <small class="text-danger" v-show="lastName.invalid">
            {{ getError('lastName') }}
          </small>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col-md-5 col-md-offset-1">
        <div class="form-group">
          <label class="control-label">
            Phone Number
          </label>
          <input class="form-control"
                 type="text"
                 name="phoneNumber"
                 v-validate="modelValidations.phoneNumber"
                 v-model="model.phoneNumber"
                 placeholder="ex: 12345678"
          />
          <small class="text-danger" v-show="phoneNumber.invalid">
            {{ getError('phoneNumber') }}
          </small>
        </div>
      </div>
      <div class="col-md-5">
        <div class="form-group">
          <label class="control-label">
            CreditCard Number
          </label>
          <input class="form-control"
                 type="text"
                 name="creditCardNumber"
                 v-validate="modelValidations.creditCardNumber"
                 v-model="model.creditCardNumber"
                 placeholder="ex: 12345678"
          />
          <small class="text-danger" v-show="creditCardNumber.invalid">
            {{ getError('creditCardNumber') }}
          </small>
        </div>
      </div>
    </div>

    <div class="row">

      <div class="col-md-5 col-md-offset-1">
        <div class="form-group">
          <label class="control-label">
            Your Age
          </label>
          <input class="form-control"
                 type="number"
                 name="age"
                 v-validate="modelValidations.age"
                 v-model="model.age"
                 placeholder="ex: 23"
          />
          <small class="text-danger" v-show="age.invalid">
            {{ getError('age') }}
          </small>
        </div>
      </div>

      <div class="col-md-5">
        <div class="form-group">
          <label class="control-label">
            Select your gender
          </label>
          <select type="text"
                  name="gender"
                  v-validate="modelValidations.gender"
                  v-model="model.gender"
                  class="form-control">
            <option selected="" disabled="">- Gender -</option>
            <option value="male">Male</option>
            <option value="female">Female</option>
            <option value="female">Others</option>
          </select>
          <small class="text-danger" v-show="gender.invalid">
            {{ getError('gender') }}
          </small>
        </div>
      </div>
    </div>
    </div>
</template>
<script>
  import {mapFields} from 'vee-validate'

  export default {
    computed: {
      ...mapFields(['firstName', 'lastName', 'phoneNumber', 'creditCardNumber', 'age', 'gender'])
    },
    data () {
      return {
        model: {
          firstName: '',
          lastName: '',
          phoneNumber: '',
          creditCardNumber: '',
          gender: '',
          age: ''
        },
        modelValidations: {
          firstName: {
            min: 2,
            required: true
          },
          lastName: {
            min: 2,
            required: true
          },
          phoneNumber: {
            required: true,
            min: 8
          },
          creditCardNumber: {
            required: true,
            min: 8
          },
          age: {
            required: true,
            min: 2,
            max: 2
          },
          gender: {
            required: true
          }
        }
      }
    },
    methods: {
      getError (fieldName) {
        return this.errors.first(fieldName)
      },
      validate () {
        return this.$validator.validateAll()
      }
    }
  }
</script>
<style>
</style>



