import DashboardLayout from '../components/BikeShare/Layout/DashboardLayout.vue'
// GeneralViews
import NotFound from '../components/GeneralViews/NotFoundPage.vue'

// Customer pages
import Register from 'src/components/BikeShare/Views/Customer/Register.vue'
import Login from 'src/components/BikeShare/Views/Customer/Login.vue'
import Maps from 'src/components/BikeShare/Views/Customer/Maps.vue'
import ReportDefective from 'src/components/BikeShare/Views/Customer/ReportDefective.vue'
import Bill from 'src/components/BikeShare/Views/Customer/Bill.vue'
import UnpaidBill from 'src/components/BikeShare/Views/Customer/UnpaidBill.vue'
import Wallet from 'src/components/BikeShare/Views/Customer/Wallet.vue'
import TraceRouteMap from 'src/components/BikeShare/Views/Customer/TraceRouteMap.vue'

// Customer Pages
let customerPage = {
  path: '/customer',
  redirect: '/',
  component: DashboardLayout,
  children: [
    {
      path: 'register',
      name: 'Register',
      component: Register
    },
    {
      path: 'login',
      name: 'Login',
      component: Login
    },
    {
      path: 'maps',
      name: 'BikeShare Maps',
      component: Maps
    },
    {
      path: 'reportdefective',
      name: 'Report Bike Defective',
      component: ReportDefective
    },
    {
      path: 'bill',
      name: 'History Bill',
      component: Bill
    },
    {
      path: 'unpaid',
      name: 'Unpaid Bill',
      component: UnpaidBill
    },
    {
      path: 'wallet',
      name: 'Wallet',
      component: Wallet
    },
    {
      path: 'traceroutemap',
      name: 'Trace Route',
      component: TraceRouteMap
    }
  ]
}

const routes = [
  {
    path: '/',
    component: DashboardLayout,
    // redirect: '/admin/overview',
    redirect: '/customer/maps'
  },
  customerPage,
  {path: '*', component: NotFound}
]

/**
 * Asynchronously load view (Webpack Lazy loading compatible)
 * The specified component must be inside the Views folder
 * @param  {string} name  the filename (basename) of the view to load.
 function view(name) {
   var res= require('../components/BikeShare/Views/' + name + '.vue');
   return res;
};**/

export default routes



