<template>
  <div>
    <h5 class="text-center">Create your account.</h5>
    <div class="row">
      <div class="col-md-5 col-md-offset-1">
        <div class="form-group">
          <label class="control-label">
            Username
          </label>
          <input class="form-control"
                 type="text"
                 name="username"
                 v-validate="modelValidations.username"
                 v-model="model.username"
                 placeholder="ex: Mike01919"
          />
          <small class="text-danger" v-show="username.invalid">
            {{ getError('username') }}
          </small>
        </div>
      </div>
      <div class="col-md-5">
        <div class="form-group">
          <label class="control-label">
            Password
          </label>
          <input class="form-control"
                 type="password"
                 name="password"
                 v-validate="modelValidations.password"
                 v-model="model.password"
                 placeholder=""
          />
          <small class="text-danger" v-show="password.invalid">
            {{ getError('password') }}
          </small>
        </div>
      </div>
    </div>

  </div>
</template>
<script>
  import {mapFields} from 'vee-validate'

  export default {
    computed: {
      ...mapFields(['username', 'password'])
    },
    data () {
      return {
        model: {
          username: '',
          password: ''
        },
        modelValidations: {
          username: {
            min: 8,
            required: true
          },
          password: {
            min: 8,
            required: true
          }
        }
      }
    },
    methods: {
      getError (fieldName) {
        return this.errors.first(fieldName)
      },
      validate () {
        return this.$validator.validateAll()
      }
    }
  }
</script>
<style>
</style>



