          {lat: 55.84470204871234, lng: -4.275248050689697}
        ]

      }
    },
    methods: {
      showSwal () {
        let flightPlanCoordinates = this.flightPlanCoordinates
        // let bikeID = this.bikeID
        swal({
          title: 'Are you sure to return the bike?',
          text: `You won't be able to revert this!`,
          type: 'warning',
          showCancelButton: true,
          confirmButtonClass: 'btn btn-success btn-fill',
          cancelButtonClass: 'btn btn-danger btn-fill',
          confirmButtonText: 'Yes, return it!',
          buttonsStyling: false
        }).then(function () {
          // return bike
          let rand = Math.round(Math.random() * flightPlanCoordinates.length)
          let latitude = flightPlanCoordinates[rand].lat
          let longitude = flightPlanCoordinates[rand].lng
          // console.log(latitude, longitude)
          let returnbikeFormData = new FormData()
          returnbikeFormData.append('customer_id', sessionStorage.getItem('customer_id'))
          returnbikeFormData.append('latitude', latitude)
          returnbikeFormData.append('longitude', longitude)
          console.log(returnbikeFormData.get('customer_id') + returnbikeFormData.get('bike_id') +
             returnbikeFormData.get('latitude') + returnbikeFormData.get('longitude'))
          axios.post('api/customer/returnbike/',
            returnbikeFormData)
            .then(response => {
              // swal(response.data)
              // console.log(response)
              sessionStorage.setItem('Ride_Bill', response.data[0].fields.Ride_Bill) // ride bike bill
              sessionStorage.setItem('Start_time', response.data[0].fields.Start_time) // ride bike start time
              sessionStorage.setItem('End_time', response.data[0].fields.End_time) // ride bike end time
              sessionStorage.setItem('returnBikeStatus', 1)
              sessionStorage.setItem('latitude', latitude)
              sessionStorage.setItem('longitude', longitude)
              swal({
                title: 'Returned!',
                text: 'Your bike has been returned.',
                type: 'success',
                confirmButtonClass: 'btn btn-success btn-fill',
                buttonsStyling: false
              }).then(
                function () {
                  // return a bike
                  // console.log('return bike  ' + bikeID + 'username' + sessionStorage.getItem('userID'))
                  window.location = '#/customer/unpaid'
                }
              )
            })
            .catch(
              function (error) { // request error
                console.log(error)
              }
            )
        }).catch(swal.noop)
      },
      // notification
      initMap (google) {
        // Regular Map
        var myLatlng = new window.google.maps.LatLng(55.816293658188606, -4.2885442276001)
        var mapOptions = {
          zoom: 12,
          center: myLatlng,
          scrollwheel: false // we disable de scroll over the map, it is a really annoing when you scroll through page
        }
        var map = new window.google.maps.Map(document.getElementById('map'), mapOptions)

        function traceroute (Coordinates, map) {
          // eslint-disable-next-line camelcase
          let fake_Coordinates = []
          const flightPath = new google.maps.Polyline({
            path: fake_Coordinates,
            geodesic: true,
            strokeColor: '#ff3333',
            strokeOpacity: 0.6, // 线条透明度[0,1]
            strokeWeight: 5 // 线条宽度
          })
          flightPath.setMap(map)
          // eslint-disable-next-line camelcase
          const trace_marker = new google.maps.Marker({
            draggable: true,   // marker 设为可拖动
            animation: google.maps.Animation.DROP, // 动画属性设为DROP
            icon: 'static/img/bike2.png'
          })
          // eslint-disable-next-line camelcase
          var refresh_time = 200 // 每更新一个点的时间间隔，单位毫秒
          for (let i = 0; i < Coordinates.length; i++) {
            setTimeout(function () {
              trace_marker.setMap(null)
              fake_Coordinates.push(Coordinates[i])
              flightPath.setPath(fake_Coordinates)
              trace_marker.setPosition(Coordinates[i])
              trace_marker.setMap(map)
              // console.log(Coordinates[i])
              // eslint-disable-next-line camelcase
            }, refresh_time * (i + 1))
          }
          setTimeout(function () {
            // eslint-disable-next-line camelcase
            trace_marker.setMap(null)
            // eslint-disable-next-line camelcase
          }, refresh_time * (Coordinates.length + 1))
        }
        const flightPlanCoordinates = this.flightPlanCoordinates

        traceroute(flightPlanCoordinates, map)
      }

    },
    mounted () {
      GoogleMapsLoader.load((google) => {
        this.initMap(google)
        // console.log(this.$route.fullPath)
      })
      // rent bike successfully
      if (sessionStorage.getItem('rentBikeStatus') === '1') {
        this.bikeID = sessionStorage.getItem('bikeID')
        this.bikeBrand = sessionStorage.getItem('bikeBrand')
      }
    },
    created () {
      // need to login
      if (sessionStorage.getItem('loginStatus') !== '1') {
        // this.user = sessionStorage.getItem('userID')
        swal({
          title: 'Error',
          text: 'Please login',
          type: 'warning',
          showCancelButton: true,
          confirmButtonClass: 'btn btn-success btn-fill',
          cancelButtonClass: 'btn btn-danger btn-fill',
          confirmButtonText: 'Login',
          buttonsStyling: false
        }).then(
          function () {
            // go to login page
            window.location = '#/customer/login'
          }, function (dismiss) {
            // dismiss can be 'overlay', 'cancel', 'close', 'esc', 'timer'
            if (dismiss === 'cancel') {
              window.location = '#/'
            }
          }
        ).catch(swal.noop)
      }
    }
  }
</script>
