<template>
  <div class="row">
    <div class="col-md-12">
      <h4 class="title">BikeShare Green Lifestyle</h4>
      <p class="category">
        check the history bill</p>
    </div>
    <div class="col-md-12 card">
      <div class="card-header">
        <div class="category">Extended tables</div>
      </div>
      <div class="card-content row">
        <div class="col-sm-6">
          <el-select
            class="select-default"
            v-model="pagination.perPage"
            placeholder="Per page">
            <el-option
              class="select-default"
              v-for="item in pagination.perPageOptions"
              :key="item"
              :label="item"
              :value="item">
            </el-option>
          </el-select>
        </div>
        <div class="col-sm-6">
          <div class="pull-right">
            <label>
              <input type="search" class="form-control input-sm" placeholder="Search records" v-model="searchQuery" aria-controls="datatables">
            </label>
          </div>
        </div>
        <div class="col-sm-12">
          <el-table class="table-striped"
                    :data="queriedData"
                    border
                    style="width: 100%">
            <el-table-column v-for="column in tableColumns"
                             :key="column.label"
                             :min-width="column.minWidth"
                             :prop="column.prop"
                             :label="column.label">
            </el-table-column>
            <el-table-column
              :min-width="120"
              fixed="right"
              label="Actions">
              <template slot-scope="props">
                <a class="btn btn-simple btn-xs btn-info btn-icon like" @click="handleLike(props.$index, props.row)"><i class="ti-money"></i></a>
                <a class="btn btn-simple btn-xs btn-warning btn-icon edit" @click="handleEdit(props.$index, props.row)"><i class="ti-pencil-alt"></i></a>
                <a class="btn btn-simple btn-xs btn-danger btn-icon remove"  @click="handleDelete(props.$index, props.row)"><i class="ti-close"></i></a>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div class="col-sm-6 pagination-info">
          <p class="category">Showing {{from + 1}} to {{to}} of {{total}} entries</p>
        </div>
        <div class="col-sm-6">
          <p-pagination class="pull-right"
                        v-model="pagination.currentPage"
                        :per-page="pagination.perPage"
                        :total="pagination.total">
          </p-pagination>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Vue from 'vue'
import {Table, TableColumn, Select, Option} from 'element-ui'
import PPagination from 'src/components/UIComponents/Pagination.vue'
import swal from 'sweetalert2'
import axios from 'axios'
Vue.use(Table)
Vue.use(TableColumn)
Vue.use(Select)
Vue.use(Option)
export default{
  components: {
    PPagination
  },
  computed: {
    pagedData () {
      return this.tableData.slice(this.from, this.to)
    },
    /***
     * Searches through table data and returns a paginated array.
     * Note that this should not be used for table with a lot of data as it might be slow!
     * Do the search and the pagination on the server and display the data retrieved from server instead.
     * @returns {computed.pagedData}
     */
    queriedData () {
      if (!this.searchQuery) {
        this.pagination.total = this.tableData.length
        return this.pagedData
      }
      let result = this.tableData
        .filter((row) => {
          let isIncluded = false
          for (let key of this.propsToSearch) {
            let rowValue = row[key].toString()
            if (rowValue.includes && rowValue.includes(this.searchQuery)) {
              isIncluded = true
            }
          }
          return isIncluded
        })
      this.pagination.total = result.length
      return result.slice(this.from, this.to)
    },
    to () {
      let highBound = this.from + this.pagination.perPage
      if (this.total < highBound) {
        highBound = this.total
      }
      return highBound
    },
    from () {
      return this.pagination.perPage * (this.pagination.currentPage - 1)
    },
    total () {
      this.pagination.total = this.tableData.length
      return this.tableData.length
    }
  },
  data () {
    return {
      pagination: {
        perPage: 5,
        currentPage: 1,
        perPageOptions: [5, 10, 25, 50],
        total: 0
      },
      searchQuery: '',
      propsToSearch: ['id'],
      tableColumns: [
        {
          prop: 'id',
          label: 'ID',
          minWidth: 50
        },
        {
          prop: 'cost',
          label: 'Cost',
          minWidth: 50
        },
        {
          prop: 'systemID',
          label: 'SystemID',
          minWidth: 50
        }
      ], // users
      tableData: [
        {
          'id': '',
          'cost': '£',
          'systemID': ''
        }
      ]
    }
  },
  methods: {
    handleLike (index, row) {
      // alert(`Your want to pay this bill ${row.id}`)
    },
    handleEdit (index, row) {
      // alert(`Your want to report defective ${row.id}`)
    },
    handleDelete (index, row) {
      let indexToDelete = this.tableData.findIndex((tableRow) => tableRow.id === row.id)
      if (indexToDelete >= 0) {
        this.tableData.splice(indexToDelete, 1)
      }
    }
  },
  mounted () {
    let getStaticsFormData = new FormData()
    getStaticsFormData.append('customer_id', sessionStorage.getItem('customer_id'))
    let tabledata = []
    axios.post('api/customer/statistics/',
      getStaticsFormData).then(
      response => {
        // console.log(response.data)
        // update tabledata
        response.data.forEach(
          function (item, index) {
            var bill = {
              'id': index,
              'cost': item.fields.Ride_Bill + '£',
              'systemID': item.pk
            }
            tabledata.push(bill)
          }
        )
      }
    ).catch(
      // console.log(error)
    )
    this.tableData = tabledata
  },
  created () {
    // need to login
    if (sessionStorage.getItem('loginStatus') !== '1') {
      // this.user = sessionStorage.getItem('userID')
      swal({
        title: 'Error',
        text: 'Please login',
        type: 'warning',
        showCancelButton: true,
        confirmButtonClass: 'btn btn-success btn-fill',
        cancelButtonClass: 'btn btn-danger btn-fill',
        confirmButtonText: 'Login',
        buttonsStyling: false
      }).then(
        function () {
          // go to login page
          window.location = '#/customer/login'
        }, function (dismiss) {
          // dismiss can be 'overlay', 'cancel', 'close', 'esc', 'timer'
          if (dismiss === 'cancel') {
            window.location = '#/'
          }
        }
      ).catch(swal.noop)
    }
  }
}
</script>
<style>
</style>



