<template>
  <div class="card">
    <form>
      <div class="card-header">
        <h4 class="card-title">
          Login
        </h4>
      </div>
      <div class="card-content">
        <div class="form-group">
          <label>Username</label>
          <input type="text"
                 name="username"
                 v-validate="modelValidations.username"
                 v-model="model.username"
                 class="form-control">
          <small class="text-danger" v-show="username.invalid">
            {{ getError('username') }}
          </small>
        </div>
        <div class="form-group">
          <label>Password</label>
          <input type="password"
                 name="password"
                 v-validate="modelValidations.password"
                 v-model="model.password"
                 class="form-control">
          <small class="text-danger" v-show="password.invalid">
            {{ getError('password') }}
          </small>
        </div>

      </div>
      <div class="card-footer text-center">
        <button type="submit"
                @click.prevent="validate"
                class="btn btn-fill btn-info btn-wd">Login</button>
        <router-link to="/customer/register">
          Register
        </router-link>
      </div>
    </form>

  </div>
</template>
<script>
  import {mapFields} from 'vee-validate'
  import axios from 'axios'
  import swal from 'sweetalert2'

  export default {
    computed: {
      ...mapFields(['username', 'password'])
    },
    data () {
      return {
        model: {
          username: '',
          password: ''
        },
        modelValidations: {
          username: {
            required: true,
            min: 8
          },
          password: {
            required: true,
            min: 8
          }
        },
        userinfo: new FormData()
      }
    },
    methods: {
      getError (fieldName) {
        return this.errors.first(fieldName)
      },
      validate () {
        // console.log(JSON.stringify(this.model))
        // alert(JSON.stringify(this.model))
        this.userinfo.append('username', this.model.username)
        this.userinfo.append('password', this.model.password)
        this.userinfo.append('user_type', 'Customer')
        // console.log(this.userinfo)
        // console.log(JSON.stringify(this.$sidebar.sidebarLinks))
        // return this.$validator.validateAll()
        // swal('Success!', 'Thanks for your time, please enjoy our services!', 'success')
        sessionStorage.setItem('userID', this.userinfo.get('username'))
        sessionStorage.setItem('loginStatus', 0)
        axios.post('api/signin/', this.userinfo).then(
          response => {
            // console.log(response)
            // console.log(response.data)
            sessionStorage.setItem('loginStatus', 1)
            sessionStorage.setItem('customer_id', response.data.pk)
            sessionStorage.setItem('balance', response.data.Customer_wallet)
            sessionStorage.setItem('session_key', response.data.session_key)
            window.location = '#/'
            // if (response.data === 'User does not exist') {
            //   sessionStorage.setItem('userID', '')
            //   swal({
            //     title: 'Error',
            //     text: 'Username does not exist .  BIKESHARE ',
            //     buttonsStyling: false,
            //     confirmButtonClass: 'btn btn-success btn-fill'
            //   }).catch(swal.noop)
            // } else if (response.data === 'Incorrect Password') {
            //   sessionStorage.setItem('userID', '')
            //   swal({
            //     title: 'Error',
            //     text: 'Incorrect Password . BIKESHARE',
            //     buttonsStyling: false,
            //     confirmButtonClass: 'btn btn-success btn-fill'
            //   }).catch(swal.noop)
            // } else {
            //   swal('Success!', 'Login success!',
            //     'success').then(
            //     function () {
            //       sessionStorage.setItem('loginStatus', 1)
            //       sessionStorage.setItem('customer_id', response.data[0].pk)
            //       window.location = '#/'
            //       // location.reload()
            //     }
            //   ).catch(swal.noop)
            // }
          }
        ).catch(function (error) {
          // console.log(error)
          if (error.response.status === 401) {
            swal({
              title: 'password incorrect',
              text: 'With your help,  we will do better.  BIKESHARE ',
              buttonsStyling: false,
              confirmButtonClass: 'btn btn-success btn-fill'
            }).then(
              function () {
                window.location = '#/'
                // location.reload()
              }
            ).catch(swal.noop)
          } else if (error.response.status === 404) {
            swal({
              title: 'username incorrect',
              text: 'With your help,  we will do better.  BIKESHARE ',
              buttonsStyling: false,
              confirmButtonClass: 'btn btn-success btn-fill'
            }).then(
              function () {
                window.location = '#/'
                // location.reload()
              }
            ).catch(swal.noop)
          } else {
            swal({
              title: 'something went wrong',
              text: 'With your help,  we will do better.  BIKESHARE ',
              buttonsStyling: false,
              confirmButtonClass: 'btn btn-success btn-fill'
            }).then(
              function () {
                window.location = '#/'
                // location.reload()
              }
            ).catch(swal.noop)
          }
        })
      }
    },
    beforeCreate () {
      // console.log('chech login: ' + sessionStorage.getItem('loginStatus'))
      let username = sessionStorage.getItem('userID')
      if (sessionStorage.getItem('loginStatus') === '1') {
        // console.log('already login')
        swal({
          title: 'BIKESHARE',
          text: username + '  Already login .  BIKESHARE ',
          buttonsStyling: false,
          confirmButtonClass: 'btn btn-success btn-fill'
        }).catch(swal.noop)
        window.location = '#/'
      }
    }
  }
</script>
<style>
</style>



