<template>
  <div class="user">
    <div class="photo">
      <img src="static/img/faces/user-face.jpg"/>
    </div>
    <div class="info">
      <a data-toggle="collapse" @click="toggleMenu" href="javascript:void(0)">
           <span>
             {{user}}
             <b class="caret"></b>
          </span>
      </a>
      <div class="clearfix"></div>
      <div>
        <el-collapse-transition>
          <ul class="nav" v-show="!isClosed">
            <li>
              <a href="#/customer/login">
                <span class="sidebar-mini">In</span>
                <span class="sidebar-normal">Login</span>
              </a>
            </li>
            <li>
              <a href="javascript:void(0)" @click="logout()">
                <span class="sidebar-mini">Out</span>
                <span class="sidebar-normal">Logout</span>
              </a>
            </li>
          </ul>
        </el-collapse-transition>
      </div>
    </div>
  </div>
</template>
<script>
  import CollapseTransition from 'element-ui/lib/transitions/collapse-transition'
  import swal from 'sweetalert2'
  export default {
    components: {
      [CollapseTransition.name]: CollapseTransition
    },
    data () {
      return {
        isClosed: true,
        user: 'User'
      }
    },
    methods: {
      toggleMenu () {
        this.isClosed = !this.isClosed
      },
      logout () {
        let username = sessionStorage.getItem('userID')
        sessionStorage.clear()
        if (sessionStorage.getItem('loginStatus') === '1') {
          sessionStorage.setItem('loginStatus', 0)
          swal({
            title: 'BIKESHARE',
            text: username + '  Already logout .  BIKESHARE ',
            buttonsStyling: false,
            confirmButtonClass: 'btn btn-success btn-fill'
          }).catch(swal.noop)
          window.location = '#/'
          this.user = 'User'
        } else {
          swal({
            title: 'BIKESHARE',
            text: 'NO user logged in .  BIKESHARE ',
            buttonsStyling: false,
            confirmButtonClass: 'btn btn-success btn-fill'
          }).catch(swal.noop)
          window.location = '#/customer/login'
          this.user = 'User'
        }
      }
    },
    updated () {
      // get username
      if (sessionStorage.getItem('loginStatus') === '1') {
        this.user = sessionStorage.getItem('userID')
      } else {
        this.user = 'User'
      }
    }
  }
</script>
<style>
  .collapsed {
    transition: opacity 1s;
  }
</style>



